  height: 0.1px;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.snapwdg2-ui-checkbox input:checked + label::before {
  border-color: #64BD72;
}

.snapwdg2-ui-checkbox input:checked + label::after {
